import * as React from 'react';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import CardMedia from '@mui/material/CardMedia';
import Typography from '@mui/material/Typography';
import { CardActionArea } from '@mui/material';

let banner = require("../images/banner_blue.png");
let location = require("../images/location1.png");
let mvv = require("../images/mvv.png");


export default function Company() {
  return (

    <>
           <Card >
            <CardActionArea>
              <CardMedia
                component="img"
                height="90"
                image={banner}
                alt="madera engenharia"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Quem Somos?
                </Typography>
                <Typography gutterBottom variant="h6" color="text.secondary">
                  Fundada em 2021 e se consolidando na trajetria de excelncia e dedicao a Madera Engenharia  uma empresa 
                  especializada em solues de engenharia em formato <em>turn-key</em>, 
                  oferecendo servios completos para solues de engenharia.
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>

           <Card >
            <CardActionArea>
              <CardMedia
                component="img"
                height="80"
                image={mvv}
                alt="madera engenharia"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Misso, Viso e Valores
                </Typography>
                <Typography gutterBottom variant="h6" color="text.secondary">
                  <p className="Tit1">Misso</p>
                    <p className="Tit2">
                    A misso da Madera Engenharia  oferecer solues de engenharia eficientes, 
                    inovadoras, seguras e sustentveis, refletindo seu compromisso em <b> ofertar 
                    engenharia de alta qualidade </b>e contribuir para o desenvolvimento eficiente e sustentvel.
                    </p> 

                    <p className="Tit1">Viso</p>
                    <p className="Tit2">
                    Com uma viso voltada em ser referncia em <b>solues de engenharia</b> na Amrica Latina, a empresa
                     busca se posicionar como primeira escolha dos clientes em inovao, eficincia e atendimento, 
                    oferecendo solues que atendam s necessidades das empresas e dos clientes.
                    </p> 

                    <p className="Tit1">Valores</p>
                    <p className="Tit2">
                    Os valores fundamentais da Madera Engenharia incluem <b>Inovao, Sustentabilidade e tica</b>, 
                    orientando suas prticas e relaes comerciais com transparncia, justia, tica e responsabilidade.
                    </p>                     
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>

           <Card >
            <CardActionArea>
              <CardMedia
                component="img"
                height="80"
                image={location}
                alt="madera engenharia"
              />
              <CardContent>
                <Typography gutterBottom variant="h5" component="div">
                  Localizao
                </Typography>
                <Typography gutterBottom variant="h6" color="text.secondary">
                  A Madera Engenharia est sediada no estado de So Paulo, atendendo a regio metropolitana
                   e expandindo sua atuao para novos mercados.
                </Typography>
              </CardContent>
            </CardActionArea>
          </Card>


    </>
  );
}
